<template>
  <nav class="twnav">
    <ul>
      <li v-for="item in routes" :key="item.name">
        <router-link :to="item.route" exact class="twnav__link">
          <span class="twnav__icon"></span>
          <!-- <img :src="fullPath === item.route ? item.activeImage : item.image" /> -->
          <span class="twnav__name">{{ item.name }}</span>
        </router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: {
    routes: {
      type: Array,
      default: () => [
        {
          name: "首页",
          route: "/tuanwei/home",
        },
        {
          name: "个人信息",
          route: "/tuanwei/profile",
        },
        {
          name: "查看报名",
          route: "/tuanwei/apply",
        },
        {
          name: "提案审核",
          route: "/tuanwei/proposal",
        },
      ],
    }
  },
  mounted() {
    let fullPath = this.$route.fullPath;
    this.fullPath = fullPath;
  },
  data() {
    return {
      fullPath: '',
      
    };
  },
};
</script>

<style lang="less">
.twnav {
