    height: 60px;
    line-height: 60px;
  }
  a {
    color: #5d5d5d;
    text-decoration: none;
    &:hover {
      color: #ff543c;
    }
    &:active {
      color: #ff543c;
    }
  }
  &__icon {
    display: inline-block;
    width: 20px;
    height: 20px;
  } 
  &__name {
