      background-image: url('../../../assets/images/home_icon_home_default.png');
    }
    &:hover .twnav__icon {
      background-image: url('../../../assets/images/home_icon_home.png');
    }
    .router-link-active .twnav__icon {
      background-image: url('../../../assets/images/home_icon_home.png');;
    }
  }
  li:nth-child(2) {
    .twnav__icon {
      background-image: url('../../../assets/images/home_icon_personal_default.png');
    }
