    }
    .router-link-active .twnav__icon {
      background-image: url('../../../assets/images/home_icon_personal.png');;
    }
  }
  li:nth-child(3) {
    .twnav__icon {
      background-image: url('../../../assets/images/home_icon_sign_default.png');
    }
    &:hover .twnav__icon {
      background-image: url('../../../assets/images/home_icon_sign.png');
    }
    .router-link-active .twnav__icon {
      background-image: url('../../../assets/images/home_icon_sign.png');;
