<template>
  <!-- twp => tuanwei profile  -->
  <div class="twp">
    <tw-header :isAdmin="true" />
    <div class="twp__body">
      <tw-nav class="twp__nav"></tw-nav>
      <div class="twp__main">
        <div class="twp__title">
          <span class="twp__title-icon"></span>
          <span class="twp__main-title">账号完善程度</span>
          <span class="twp__sub-title">(建议您完善个人信息，以便进行报名、提交提案后续操作)</span>
          <span class="twp__greet">{{ profile.username }}，您好!</span>
        </div>
        <div class="twp__progress">
          <div class="twp__label">进度</div>
          <div class="twp__progress-bar progress-bar">
            <div class="progress-bar__bg"></div>
            <div class="progress-bar__bar"></div>
          </div>
          <div class="twp__progress-percent">20%</div>
        </div>
        <div class="twp__seperator"></div>
        <div class="twp__form">
          <div class="twp__form-label">账号</div>
          <div class="twp__form-input"><input v-model="profile.username" readonly disabled name="account" type="text"></div>
          <div class="twp__form-icon"><img src="../../../assets/images/home_icon_filled.png" alt=""></div>
        </div>
        <div class="twp__form">
          <div class="twp__form-label">姓名</div>
          <div class="twp__form-input"><input v-model="profile.nick" name="name" type="text"></div>
          <div class="twp__form-icon"><img src="../../../assets/images/home_icon_filled.png" alt=""></div>
        </div>
        <div class="twp__form">
          <div class="twp__form-label">性别</div>
          <div class="twp__form-input">
            <label for="gender-male">
              <input v-model="profile.sex" type="radio" id="gender-male" name="gender" value="1" />男
            </label>
            <label for="gender-female">
              <input v-model="profile.sex" type="radio" id="gender-female" name="gender" value="2" />女
            </label>
          </div>
          <div class="twp__form-icon"><img src="../../../assets/images/home_icon_filled.png" alt=""></div>
        </div>
        <div class="twp__form">
          <div class="twp__form-label">手机号</div>
          <div class="twp__form-input"><input v-model="profile.phone" readonly disabled type="text"></div>
          <div class="twp__form-icon"><img src="../../../assets/images/home_icon_filled.png" alt=""></div>
        </div>
        <button class="twp__save-btn btn2" @click="doUpdateAdminProfile">保存</button>
      </div>
    </div>
    <tw-footer class="twp__footer" />
  </div>
</template>

<script>
import TwNav from '../common/nav';
import TwHeader from '../common/header';
import TwFooter from '../common/footer';
import { getAdminProfile, updateAdminProfile } from '@/api/admin.js';

export default {
  components: {
    TwNav,
    TwHeader,
    TwFooter,
  },
  created() {
    this.doGetAdminProfile();
  },
  data() {
    return {
      profile: {
        username: '',
        nick: '',
        sex: 0,
        phone: ''
      },
      percent: 0
    }
  },
  methods: {
    doGetAdminProfile() {
      getAdminProfile()
      .then(res => {
        let data = res.data.data;
        this.profile = data;
        console.log('tuanwei profile', data);
      })
    },
    doUpdateAdminProfile() {
      let body = {
        realname: this.profile.nick,
        sex: this.profile.sex
      }
      updateAdminProfile(body)
      .then(res => {
        console.log('update admin profile result', res);
      })
      .catch(err => {
        console.error(err);
      })
    }
  }
};
</script>

